import { React, useState } from "react";
import {
  QuestionMarkCircleIcon,
  DocumentTextIcon,
  VolumeUpIcon,
  ChevronUpIcon,
  ChatIcon,
  ChevronDownIcon,
} from "@heroicons/react/outline";
import { Link } from "react-router-dom";
import Menu from "images/menu.svg";
import Logo from "images/logo-lg.png";
import { Button, IconButton } from "@material-ui/core";
import Dropdown from "components/common/dropdown";
import GasPrice from "components/common/gas-price";
import DodoDetailModal from "components/common/dodo-detail-modal";
function NavRight() {
  const dropOptionsChild = [
    {
      value: "dropOptionsChild",
    },
    {
      value: "dropOptionsChild",
    },
    {
      value: "dropOptionsChild",
    },
  ];
  const endpoint = [
    {
      value: "Global",
    },
    {
      value: "APAC",
    },
    {
      value: "N.American",
    },
  ];
  const language = [
    {
      value: "English",
    },
    {
      value: "Urdu",
    },
  ];
  const network = [
    {
      value: "ETH",
    },
    {
      value: "BSC",
    },
    {
      value: "Heco",
    },
    {
      value: "Polygon",
    },
  ];
  const dropOptions = [
    {
      name: "Gas Price",
      detail: "Gas Price",
    },
    {
      name: "Endpoint",
      detail: "Endpoint",
    },
    {
      name: `Language`,
      detail: `Language`,
    },
    {
      name: `Network`,
      detail: `Network`,
    },
  ];
  const bell = [
    {
      id: "1",
      to: "",
      name: "Help",
      icon: QuestionMarkCircleIcon,
    },
    {
      id: "2",
      to: "",
      name: "Announcement",
      icon: VolumeUpIcon,
    },
    {
      id: "3",
      to: "",
      name: `Forum`,
      icon: ChatIcon,
    },
    {
      id: "4",
      to: "",
      name: `Docs`,
      icon: DocumentTextIcon,
    },
  ];
  const [show, setShow] = useState(false);
  const [active, setActive] = useState();
  const showToggle = (key) => {
    setActive(key);
    setShow(!show);
  };

  const [open, setOpen] = useState(false);
  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <>
      <IconButton
        onClick={handleClickOpen}
        color="primary"
        className="bg-primary"
        aria-label="delete"
      >
        <div className="h-5 w-5 rounded-full overflow-hidden">
          <img src={Logo} alt="Logo" className="h-5 w-5" />
        </div>
      </IconButton>
      <DodoDetailModal
        onClick={handleClickOpen}
        open={open}
        onClose={handleClose}
      />
      <div className="flex items-center rounded-full bg-primary pl-4">
        <p className="flex items-center pr-2 text-white">
          <div className="bg-gray-400 h-2.5 w-2.5 rounded-full flex items-center justify-center mr-1">
            <div className="rounded-full h-1 w-1 bg-white"></div>
          </div>
          Eth
        </p>
        <Button
          variant="outlined"
          classes={{
            root: "rounded-full tracking-normal pt-1 pb-1.5 md:text-md text-sm",
          }}
          color="secondary"
        >
          Connect to a wallet
        </Button>
      </div>
      <Dropdown
        mainMenuClass="w-78 py-2"
        menuItem={
          <>
            <img src={Menu} alt="Menu" className="cursor-pointer" />
          </>
        }
        options={
          <div className="divide-y divide-gray-640 space-y-2">
            <div>
              {dropOptions.map((item, key) => (
                <>
                  <div
                    className="flex items-center justify-between py-3 px-4 space-x-3 cursor-pointer"
                    onClick={() => showToggle(key)}
                  >
                    <p className="text-sm font-medium">
                      {item.name}
                    </p>
                    <p className="text-base flex items-center text-gray-100 font-medium">
                      {item.detail}
                      {show === true ? (
                        <ChevronUpIcon className="w-3 ml-2 cursor-pointer" />
                      ) : (
                        <>
                          <ChevronDownIcon className="w-3 ml-2 cursor-pointer" />
                        </>
                      )}
                    </p>
                  </div>
                  {show === true ? (
                    <div className="px-4">
                      {active === 0 && key === active ? (
                        <GasPrice dropOptionsChild={dropOptionsChild} />
                      ) : active === 1 && key === active ? (
                        <GasPrice
                          inputClass="hidden"
                          dropOptionsChild={endpoint}
                        />
                      ) : active === 2 && key === active ? (
                        <GasPrice
                          containerClass="grid grid-cols-3 gap-3 py-2"
                          inputClass="hidden"
                          dropOptionsChild={language}
                        />
                      ) : active === 3 && key === active ? (
                        <GasPrice
                          containerClass="grid grid-cols-3 gap-3 py-2"
                          inputClass="hidden"
                          dropOptionsChild={network}
                        />
                      ) : (
                        ""
                      )}
                    </div>
                  ) : (
                    <></>
                  )}
                </>
              ))}
            </div>
            <div className="pt-2">
              {bell.map((item, key) => (
                <Link to={item.to}>
                  <div className="flex items-center py-3 px-4 space-x-2 hover:bg-gray-800 cursor-pointer">
                    <item.icon
                      className="flex-shrink-0 h-4.5 w-4.5 text-white"
                      aria-hidden="true"
                    />
                    <p className="text-white text-sm font-medium">
                      {item.name}
                    </p>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        }
      />
    </>
  );
}

export default NavRight;
